// components/Layout.tsx

import React, { ReactNode } from 'react';
import Link from 'next/link';
import { useRouter } from 'next/router';
import { supabase } from '../lib/supabaseClient';
import { Home, Users, DollarSign, LogOut, Settings } from 'lucide-react'; // Ícones para o menu

interface LayoutProps {
  children: ReactNode;
  title: string; // Título da página atual
}

const Layout: React.FC<LayoutProps> = ({ children, title }) => {
  const router = useRouter();

  const handleLogout = async () => {
    // Realiza o logout no Supabase
    const { error } = await supabase.auth.signOut();
    if (error) {
      console.error('Erro ao fazer logout:', error);
    } else {
      // Redireciona para a página de login após o logout
      router.push('/login');
    }
  };

  // Define os itens de navegação da sidebar
  const navItems = [
    { name: 'Dashboard', href: '/home-acao', icon: <Home className="w-5 h-5" /> },
    { name: 'Elenco', href: '/elenco', icon: <Users className="w-5 h-5" /> },
    { name: 'Financeiro', href: '/financeiro', icon: <DollarSign className="w-5 h-5" /> }, // Página futura (adicionar rota se necessário)
    // { name: 'Configurações', href: '/settings', icon: <Settings className="w-5 h-5" /> }, // Página futura
  ];

  return (
    // Aplica o fundo escuro (graphite) e o texto claro (light) a TODO o layout
    // Usa flex para alinhar a sidebar e o conteúdo principal lado a lado
    <div className="min-h-screen bg-dribla-graphite text-dribla-light flex">
      
      {/* Sidebar de Navegação Fixa */}
      <aside className="w-64 bg-gray-900 border-r border-gray-700 p-6 flex flex-col justify-between">
        <div>
          {/* Logo/Título na Sidebar */}
          <Link href="/home-acao">
             <span className="text-3xl font-bold text-dribla-green mb-10 block cursor-pointer">Dribla</span>
          </Link>
          
          {/* Itens de Navegação */}
          <nav className="space-y-4">
            {navItems.map((item) => (
              <Link key={item.name} href={item.href} legacyBehavior>
                <a 
                  className={`flex items-center space-x-3 px-4 py-2 rounded-lg transition duration-200 
                    ${router.pathname === item.href 
                      ? 'bg-dribla-green text-gray-900 font-bold shadow-md' // Estilo ativo
                      : 'text-gray-300 hover:bg-gray-700 hover:text-white' // Estilo inativo/hover
                    }`}
                >
                  {item.icon}
                  <span>{item.name}</span>
                </a>
              </Link>
            ))}
          </nav>
        </div>
        
        {/* Botão de Logout na base da Sidebar */}
        <button 
          onClick={handleLogout} 
          className="flex w-full items-center space-x-3 px-4 py-2 rounded-lg text-gray-300 hover:bg-red-800 hover:text-white transition duration-200 mt-8"
        >
          <LogOut className="w-5 h-5" />
          <span>Sair</span>
        </button>
      </aside>

      {/* Conteúdo Principal (Ocupa o restante da tela) */}
      <div className="flex-1 flex flex-col">
          {/* Cabeçalho da Seção Principal (Título da Página) */}
          <header className="px-8 py-6 border-b border-gray-700">
            <h1 className="text-3xl font-bold text-white">{title}</h1>
          </header>
          
          {/* Área de Conteúdo da Página Atual */}
          <main className="flex-1 p-8 overflow-y-auto"> 
            {/* O children é onde o conteúdo de /elenco, /home-acao, etc., será renderizado */}
            {children} 
          </main>
      </div>
    </div>
  );
};

export default Layout;

