// pages/elenco.tsx

import React, { useState, useEffect } from 'react';
import Head from 'next/head';
import Layout from '../components/Layout';
// IMPORTANDO O MODAL SEPARADO
import ModalNovoAluno from '../components/ModalNovoAluno';
import { supabase } from '../lib/supabaseClient';
import { useRouter } from 'next/router';
import { Loader2, Plus, Edit, Trash2 } from 'lucide-react';
import Link from 'next/link';

// Tipagem para os dados de Aluno (garante consistência)
interface Aluno {
  id: number;
  nome_aluno: string;
  nome_pai: string;
  email_pai: string;
  data_matricula: string; // ou Date, se preferir objetos Date
  data_vencimento_mensalidade: string;
  status_mensalidade: 'paga' | 'proximo' | 'vencida';
  valor_mensalidade: number;
}

const ElencoPage: React.FC = () => {
  const router = useRouter();
  const [alunos, setAlunos] = useState<Aluno[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [showModal, setShowModal] = useState(false); // Estado para controlar a visibilidade do modal
  // Lê o filtro inicial da URL ou define 'todos' como padrão
  const initialFilter = router.query.filtro === 'vencida' ? 'atrasados' : router.query.filtro === 'proximo' ? 'proximo' : 'todos';
  const [currentFilter, setCurrentFilter] = useState<'todos' | 'atrasados' | 'proximo' | 'paga'>(initialFilter as 'todos' | 'atrasados' | 'proximo' | 'paga');


  // Função para buscar os alunos no Supabase
  const fetchAlunos = async () => {
    setLoading(true);
    setError(null);

    const { data: { user } } = await supabase.auth.getUser();
    if (!user) {
      router.push('/login'); // Redireciona se não estiver logado
      return;
    }

    // Monta a query base
    let query = supabase
      .from('alunos')
      .select('*')
      .eq('id_treinador', user.id); // Filtra sempre pelo treinador logado

    // Aplica o filtro de status selecionado
    if (currentFilter === 'atrasados') {
        query = query.eq('status_mensalidade', 'vencida');
    } else if (currentFilter === 'proximo') {
        query = query.eq('status_mensalidade', 'proximo');
    } else if (currentFilter === 'paga') {
        query = query.eq('status_mensalidade', 'paga');
    }
    // 'todos' não precisa de filtro extra de status

    // Ordena por nome do aluno
    const { data, error: fetchError } = await query.order('nome_aluno', { ascending: true });

    if (fetchError) {
      console.error("Erro ao buscar alunos:", fetchError);
      setError("Não foi possível carregar o elenco. Tente novamente.");
      setLoading(false);
      return;
    }

    // Atualiza o estado com os alunos encontrados
    setAlunos(data || []);
    setLoading(false);
  };

  // useEffect para buscar alunos quando o componente monta ou o filtro muda
  useEffect(() => {
    fetchAlunos();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentFilter]); // Dependência: re-executa quando currentFilter muda

  // Função chamada pelo Modal após um aluno ser adicionado com sucesso
  const handleAlunoAdicionado = () => {
    setShowModal(false); // Fecha o modal
    fetchAlunos(); // Recarrega a lista para mostrar o novo aluno
  };

  // Formata a data (DD/MM/YYYY) - Garante tratamento UTC
  const formatDate = (dateString: string | null) => {
     if (!dateString) return '-';
     const date = new Date(dateString + 'T00:00:00Z'); // Interpreta como UTC
     const options: Intl.DateTimeFormatOptions = { year: 'numeric', month: '2-digit', day: '2-digit', timeZone: 'UTC' };
     return date.toLocaleDateString('pt-BR', options);
  };

  // Formata o valor monetário (R$ XX,XX)
  const formatCurrency = (value: number | null) => {
    if (value === null || value === undefined) return 'R$ 0,00';
    return value.toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' });
  };

  // Mapeamento de status para texto e classes CSS para os badges
  const statusMap = {
      paga: { text: 'Pago', className: 'bg-green-100 text-green-800' },
      proximo: { text: 'Próximo', className: 'bg-blue-100 text-blue-800' },
      vencida: { text: 'Vencido', className: 'bg-red-100 text-red-800' }
  };

  return (
    <>
      <Head>
        <title>Dribla | Elenco</title>
      </Head>
      <Layout title="Gestão de Alunos"> {/* Usa o Layout com sidebar */}
        <div className="flex flex-col sm:flex-row justify-between items-center mb-6 gap-4">
          {/* Botão para abrir o modal */}
          <button
            onClick={() => setShowModal(true)} // Ação de clique para mostrar o modal
            className="w-full sm:w-auto flex items-center justify-center px-5 py-2 bg-dribla-green text-gray-900 font-bold rounded-lg hover:bg-green-600 transition duration-200 shadow-md"
          >
            <Plus className="w-5 h-5 mr-2" /> Novo Atleta
          </button>

          {/* Botões de Filtro */}
          <div className="flex flex-wrap justify-center space-x-2">
            {/* Botão Todos */}
            <button
                onClick={() => setCurrentFilter('todos')}
                className={`px-4 py-2 rounded-lg text-sm font-medium transition duration-150 ${currentFilter === 'todos' ? 'bg-dribla-green text-gray-900 shadow-md' : 'bg-gray-700 text-gray-300 hover:bg-gray-600'}`}
            >
                Todos
            </button>
            {/* Botão Atrasados */}
            <button
                onClick={() => setCurrentFilter('atrasados')}
                className={`px-4 py-2 rounded-lg text-sm font-medium transition duration-150 ${currentFilter === 'atrasados' ? 'bg-dribla-orange text-gray-900 shadow-md' : 'bg-gray-700 text-gray-300 hover:bg-gray-600'}`}
            >
                Atrasados
            </button>
             {/* Botão Próximos */}
            <button
                onClick={() => setCurrentFilter('proximo')}
                className={`px-4 py-2 rounded-lg text-sm font-medium transition duration-150 ${currentFilter === 'proximo' ? 'bg-blue-500 text-white shadow-md' : 'bg-gray-700 text-gray-300 hover:bg-gray-600'}`}
            >
                Próximos
            </button>
             {/* Botão Pagos */}
             <button
                onClick={() => setCurrentFilter('paga')}
                className={`px-4 py-2 rounded-lg text-sm font-medium transition duration-150 ${currentFilter === 'paga' ? 'bg-gray-500 text-white shadow-md' : 'bg-gray-700 text-gray-300 hover:bg-gray-600'}`}
            >
                Pagos
            </button>
          </div>
        </div>

        {/* Indicador de Carregamento */}
        {loading && (
          <div className="flex justify-center items-center py-10">
            <Loader2 className="w-10 h-10 text-dribla-green animate-spin" />
          </div>
        )}

        {/* Mensagem de Erro */}
        {error && (
          <p className="text-red-500 text-center mt-8 bg-red-900/30 p-4 rounded-lg border border-red-700">{error}</p>
        )}

        {/* Estado Vazio (Sem alunos no filtro) */}
        {!loading && !error && alunos.length === 0 && (
          <div className="bg-gray-800 p-8 rounded-lg text-center text-gray-400 border border-gray-700 shadow-lg">
            <p className="text-xl font-semibold mb-3 text-white">Nenhum atleta encontrado.</p>
            <p className='mb-6'>Parece que você ainda não cadastrou atletas ou não há atletas neste filtro.</p>
             <button
                onClick={() => setShowModal(true)} // Botão para adicionar o primeiro atleta
                className="flex items-center justify-center mx-auto px-5 py-2 bg-dribla-green text-gray-900 font-bold rounded-lg hover:bg-green-600 transition duration-200 shadow-md"
            >
                <Plus className="w-5 h-5 mr-2" /> Adicionar Primeiro Atleta
            </button>
          </div>
        )}

        {/* Tabela de Alunos (quando há dados) */}
        {!loading && !error && alunos.length > 0 && (
          <div className="overflow-x-auto bg-gray-800 rounded-lg shadow-xl border border-gray-700">
            <table className="min-w-full divide-y divide-gray-700">
              <thead className="bg-gray-700">
                <tr>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">
                    Aluno
                  </th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">
                    Responsável
                  </th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">
                    Vencimento
                  </th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">
                    Valor
                  </th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">
                    Status
                  </th>
                  <th scope="col" className="relative px-6 py-3">
                    <span className="sr-only">Ações</span>
                  </th>
                </tr>
              </thead>
              <tbody className="divide-y divide-gray-700">
                {alunos.map((aluno) => (
                  <tr key={aluno.id} className="hover:bg-gray-700/50 transition duration-150">
                    {/* Colunas da Tabela */}
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="text-sm font-medium text-white">{aluno.nome_aluno}</div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="text-sm text-gray-300">{aluno.nome_pai}</div>
                      <div className="text-xs text-gray-500">{aluno.email_pai}</div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="text-sm text-gray-300">{formatDate(aluno.data_vencimento_mensalidade)}</div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="text-sm text-gray-300">{formatCurrency(aluno.valor_mensalidade)}</div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      {/* Badge de Status Colorido */}
                      <span className={`px-3 py-1 inline-flex text-xs leading-5 font-semibold rounded-full
                        ${statusMap[aluno.status_mensalidade]?.className ?? ''}
                      `}>
                        {statusMap[aluno.status_mensalidade]?.text ?? 'Desconhecido'}
                      </span>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                      {/* Botões de Ação (Editar/Excluir - Lógica a implementar) */}
                      <button className="text-blue-400 hover:text-blue-300 mx-2" title="Editar Aluno">
                        <Edit className="w-5 h-5" />
                      </button>
                      <button className="text-red-400 hover:text-red-300 mx-2" title="Excluir Aluno">
                        <Trash2 className="w-5 h-5" />
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}

        {/* Renderiza o Modal se showModal for true */}
        {showModal && (
          <ModalNovoAluno
            onClose={() => setShowModal(false)} // Passa a função para fechar
            onAlunoAdicionado={handleAlunoAdicionado} // Passa o callback de sucesso
          />
        )}
      </Layout>
    </>
  );
};

export default ElencoPage;

