// components/ModalNovoAluno.tsx

import React, { useState } from 'react';
import { supabase } from '../lib/supabaseClient';
import { Loader2 } from 'lucide-react';

interface ModalNovoAlunoProps {
  onClose: () => void;
  onAlunoAdicionado: () => void; // Callback para atualizar a lista
}

// Define os tipos de status que podemos SALVAR inicialmente
type StatusInicialPossivel = 'paga' | 'vencida';

const ModalNovoAluno: React.FC<ModalNovoAlunoProps> = ({ onClose, onAlunoAdicionado }) => {
  const [nome, setNome] = useState('');
  const [nomePai, setNomePai] = useState('');
  const [emailPai, setEmailPai] = useState('');
  const [valor, setValor] = useState<number | ''>('');
  // Padrão para hoje no formato YYYY-MM-DD
  const hoje = new Date().toISOString().slice(0, 10);
  const [vencimento, setVencimento] = useState<string>(hoje);
  // NOVO ESTADO: Controla se o pagamento inicial foi feito (true = pago, false = não pago)
  const [foiPagoInicialmente, setFoiPagoInicialmente] = useState<boolean>(true); // Padrão 'pago'
  const [loadingModal, setLoadingModal] = useState(false);
  const [errorModal, setErrorModal] = useState<string | null>(null);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoadingModal(true);
    setErrorModal(null);

    // Validação Simples
    if (!nome || !emailPai || valor === '' || !vencimento) {
      setErrorModal('Preencha todos os campos obrigatórios.');
      setLoadingModal(false);
      return;
    }
     if (valor < 0) {
       setErrorModal('O valor da mensalidade não pode ser negativo.');
       setLoadingModal(false);
       return;
    }

    // Verificação de Limite de Alunos (código omitido por brevidade - continua igual)
    const { data: { user } } = await supabase.auth.getUser();
    if (!user) {
      setErrorModal('Usuário não autenticado. Faça login novamente.');
      setLoadingModal(false);
      return;
    }

    try {
        // Busca dados do treinador e conta alunos (código omitido por brevidade - continua igual)
        const { data: treinadorData, error: treinadorError } = await supabase.from('treinadores').select('plano_atual, limite_alunos').eq('id', user.id).single();
        if (treinadorError) throw treinadorError;
        const { count: alunosAtuais, error: countError } = await supabase.from('alunos').select('*', { count: 'exact', head: true }).eq('id_treinador', user.id);
        if (countError) throw countError;
        if (treinadorData.plano_atual !== 'premium' && (alunosAtuais ?? 0) >= treinadorData.limite_alunos) {
             setErrorModal(`Limite de ${treinadorData.limite_alunos} alunos atingido para o plano ${treinadorData.plano_atual.toUpperCase()}. Faça upgrade.`);
             setLoadingModal(false);
             return;
        }


        // --- LÓGICA CORRIGIDA PARA STATUS INICIAL ---
        let statusParaSalvar: StatusInicialPossivel = 'paga'; // Padrão

        if (!foiPagoInicialmente) {
            // Converte a data de vencimento (string YYYY-MM-DD) para um objeto Date (UTC)
            const dataVencimentoObj = new Date(vencimento + 'T00:00:00Z');
            // Pega a data de hoje (meia-noite UTC) para comparação correta
            const hojeObj = new Date();
            hojeObj.setUTCHours(0, 0, 0, 0);

            // Se a data de vencimento for ANTERIOR a hoje, marca como vencida
            if (dataVencimentoObj < hojeObj) {
                statusParaSalvar = 'vencida';
            }
            // Se for hoje ou no futuro, mantém 'paga' (a automação pegará depois)
        }
        // Se foiPagoInicialmente for true, o status já é 'paga'

        // --- Inserção do Aluno ---
        const payload = {
          nome_aluno: nome,
          nome_pai: nomePai || 'Não informado',
          email_pai: emailPai,
          data_matricula: hoje,
          data_vencimento_mensalidade: vencimento, // Salva a data escolhida
          // Usa o status calculado
          status_mensalidade: statusParaSalvar,
          valor_mensalidade: valor,
          id_treinador: user.id
        };

        const { error: insertError } = await supabase.from('alunos').insert([payload]);

        if (insertError) {
          console.error('Erro ao inserir aluno:', insertError);
          throw new Error('Erro ao cadastrar atleta. Verifique os dados e tente novamente.');
        }

        // Sucesso!
        setLoadingModal(false);
        onAlunoAdicionado();

    } catch (err: any) {
        console.error("Erro no Modal:", err);
        setErrorModal(err.message || 'Ocorreu um erro inesperado.');
        setLoadingModal(false);
    }
  };

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-70 backdrop-blur-sm">
      <div className="bg-gray-800 p-8 rounded-xl shadow-2xl z-10 w-full max-w-lg border border-gray-700">
        <h2 className="text-2xl font-bold mb-6 text-white text-center">Adicionar Novo Atleta</h2>

        {errorModal && <p className="text-red-400 bg-red-900/50 p-3 rounded mb-4 text-center">{errorModal}</p>}

        <form onSubmit={handleSubmit} className="space-y-4">
          {/* Campos Nome Aluno, Nome Pai, Email Pai (sem alterações) */}
          <div>
            <label htmlFor="nome-aluno" className="block text-sm font-medium text-gray-300 mb-1">Nome do Atleta*</label>
            <input id="nome-aluno" value={nome} onChange={(e) => setNome(e.target.value)} required className="w-full input-dribla" placeholder="Nome completo do aluno"/>
          </div>
          <div>
            <label htmlFor="nome-pai" className="block text-sm font-medium text-gray-300 mb-1">Nome do Responsável</label>
            <input id="nome-pai" value={nomePai} onChange={(e) => setNomePai(e.target.value)} className="w-full input-dribla" placeholder="(Opcional)"/>
          </div>
          <div>
            <label htmlFor="email-pai" className="block text-sm font-medium text-gray-300 mb-1">Email do Responsável*</label>
            <input id="email-pai" value={emailPai} onChange={(e) => setEmailPai(e.target.value)} type="email" required className="w-full input-dribla" placeholder="Para envio de cobranças"/>
          </div>

          {/* Linha para Valor e Vencimento (sem alterações) */}
           <div className="flex flex-col sm:flex-row space-y-4 sm:space-y-0 sm:space-x-4">
            <div className="flex-1">
              <label htmlFor="valor" className="block text-sm font-medium text-gray-300 mb-1">Valor Mensalidade (R$)*</label>
              <input id="valor" value={valor} onChange={(e) => setValor(e.target.value === '' ? '' : Number(e.target.value))} type="number" min="0" step="0.01" required className="w-full input-dribla" placeholder="Ex: 100.00"/>
            </div>
            <div className="flex-1">
              <label htmlFor="vencimento" className="block text-sm font-medium text-gray-300 mb-1">Próximo Vencimento*</label>
              <input id="vencimento" value={vencimento} onChange={(e) => setVencimento(e.target.value)} type="date" required className="w-full input-dribla"/>
            </div>
          </div>

          {/* CAMPO CORRIGIDO: Status Inicial (Pago / Não Pago) */}
          <div>
             <label className="block text-sm font-medium text-gray-300 mb-2">Status da Mensalidade na Matrícula*</label>
             <div className="flex items-center space-x-6">
                {/* Opção Pago */}
                <label className="flex items-center cursor-pointer">
                    <input
                        type="radio"
                        name="statusInicial"
                        checked={foiPagoInicialmente === true} // Liga ao estado booleano
                        onChange={() => setFoiPagoInicialmente(true)}
                        className="form-radio h-4 w-4 text-dribla-green bg-gray-600 border-gray-500 focus:ring-dribla-green"
                    />
                    <span className="ml-2 text-sm text-gray-300">Pago</span>
                </label>
                {/* Opção Não Pago */}
                <label className="flex items-center cursor-pointer">
                    <input
                        type="radio"
                        name="statusInicial"
                        checked={foiPagoInicialmente === false} // Liga ao estado booleano
                        onChange={() => setFoiPagoInicialmente(false)}
                         className="form-radio h-4 w-4 text-dribla-orange bg-gray-600 border-gray-500 focus:ring-dribla-orange"
                    />
                    <span className="ml-2 text-sm text-gray-300">Não Pago</span>
                </label>
             </div>
             {/* Ajuda contextual */}
             {!foiPagoInicialmente && (
                <p className="mt-2 text-xs text-gray-400">
                    Se a data de vencimento acima já passou, o status será 'Vencido'. Caso contrário, será 'Pago' (pendente).
                </p>
             )}
          </div>

          {/* Botões de Ação (sem alterações) */}
          <div className="flex justify-end space-x-3 pt-4">
            <button type="button" onClick={onClose} className="btn-secondary">
              Cancelar
            </button>
            <button type="submit" disabled={loadingModal} className="btn-primary flex items-center">
              {loadingModal ? <Loader2 className="w-5 h-5 animate-spin mr-2" /> : null}
              {loadingModal ? 'Salvando...' : 'Adicionar Atleta'}
            </button>
          </div>
        </form>
      </div>
       {/* Estilos Globais JSX (sem alterações) */}
      <style jsx global>{`
        .input-dribla { /* ... estilos ... */ }
        .input-dribla:focus { /* ... estilos ... */ }
         .btn-primary { /* ... estilos ... */ }
        .btn-primary:hover { /* ... estilos ... */ }
         .btn-primary:disabled { /* ... estilos ... */ }
        .btn-secondary { /* ... estilos ... */ }
        .btn-secondary:hover { /* ... estilos ... */ }
        /* Estiliza os radio buttons */
        .form-radio { appearance: none; border-radius: 50%; width: 1rem; height: 1rem; border: 1px solid rgb(75 85 99); background-color: rgb(55 65 81); transition: background-color 0.2s; }
        .form-radio:checked { background-color: currentColor; border-color: currentColor; background-image: url("data:image/svg+xml,%3csvg viewBox='0 0 16 16' fill='white' xmlns='http://www.w3.org/2000/svg'%3e%3ccircle cx='8' cy='8' r='3'/%3e%3c/svg%3e"); }
        .form-radio:focus { outline: 2px solid transparent; outline-offset: 2px; box-shadow: var(--tw-ring-inset) 0 0 0 calc(2px + var(--tw-ring-offset-width)) var(--tw-ring-color); }

      `}</style>
    </div>
  );
};

export default ModalNovoAluno;

